var getType = function(o) {
    var type = Object.prototype.toString.call(o).toLocaleLowerCase().replace('[object ', '').replace(']', '');
    if (type === 'object' && o && o.hasOwnProperty('length')) {
        return 'array';
    }
    return type;
};

function proxyJdk(bh, mobileSDK) {
    mobileSDK.bh = bh;
    //拍照
    mobileSDK.takeCamera = function() {
        var args = arguments;
        var callback;
        var isfront = false;
        if (getType(args[0]) === 'boolean') {
            isfront = args[0];
            if (getType(args[1]) === 'function') {
                callback = args[1];
            }
        }
        if (getType(args[0]) === 'function') {
            callback = args[0];
        }
        return bh.systemAbility.takeCamera(function(ret) {
            callback && callback(ret);
        });
    };
    //选择图片
    mobileSDK.takePhoto = function() {
        var args = arguments;
        var callback;
        if (getType(args[0]) === 'function') {
            callback = args[0];
        }
        var limit = 1;
        if (getType(args[1]) === 'number') {
            limit = args[1];
        }
        return bh.systemAbility.takePhoto(function(ret) {
            callback && callback(ret);
        }, limit);
    };
    //预览图片
    mobileSDK.preViewImages = function() {
        return bh.UI.preViewImages.apply(bh.UI, arguments);
    };
    //关闭页面
    mobileSDK.closeWebView = function() {
        return bh.UI.closeWebView();
    };
    //扫描二维码或者一维码
    mobileSDK.scan = function() {
        return bh.qrcode.scan.apply(bh.qrcode, arguments);
    };
    //上传图片到emap
    mobileSDK.uploadImgsToEmap = function(opt) {
        console.log('mixin_sdk_uploadImgsToEmap',opt);
        return bh.wisedu.uploadToEMAP(opt.host, opt.urls, opt.config || {});
    };
    //改变标题
    mobileSDK.setTitleText = function(opt = '') {
        return bh.UI.setTitleText(opt);
    };
    //获取当前位置
    mobileSDK.getCurrentPosition = function() {
        bh.geolocation.getCurrentPosition.apply(bh.geolocation, arguments);
    };
    //获取设备Id
    mobileSDK.getDeviceId = function(callback) {
        if(typeof callback === 'function'){
            bh.device.getInfo(function (_res) {
                callback({
                    type: 'success',
                    data: {
                        uuid: _res.uuid
                    }
                });
            }, function (err) {
                callback({
                    type: 'error',
                    message: err
                });
            });
        }
    };
    mobileSDK.share = function(param){
        var body = $('body');
        var shareImg = 'data:image/png;base64,';
        var scrollY = window.top.scrollY || 0;
        var sHtml = '<div class="bh-mixin-share" style="position:absolute;top:'+scrollY+'px;left:0;width: 100%;height: 100%;background: rgba(0,0,0,0.7);"><img style="width:100%;" src="'+shareImg+'"><div>';
        body.append(sHtml);
        body.css('overflow','hidden');
        //注册点击事件,点击后删除遮罩
        $(".bh-mixin-share").click(function(e){
            body.css('overflow','auto');
            $(".bh-mixin-share").remove();
        });

        bh.social.mamp.social.shareAll ({
            title:param.title,
            content:param.desc,
            iconUrl:param.imgUrl,
            linkUrl:param.link
        });
    }
}

export default proxyJdk;



